export default function (state = {}, action) {
    const newState = {};
	switch (action.type) {
        case 'USER_LOGIN':
            newState.logged = true;
            newState.user = action.user;
            return newState;
        case 'USER_LOGOUT':
            newState.logged = false;
            return newState;
        default:
            return state;
    }
}



